import { createApp } from 'vue';
import { createStore } from 'vuex';
import { BASE_API_URL } from './index.js';

const store = createStore({
    state: {
        searchQuery: '',
        weather: null,
    },
    mutations: {
        SET_SEARCH_QUERY: (state, value) => {
            state.searchQuery = value;
        },
        SET_FORECAST_TO_STATE: (state, weather) => {
            state.weather = weather;
        },
    },
    actions: {
        GET_SEARCH_QUERY({commit}, value) {
            commit('SET_SEARCH_QUERY', value);
        },
        async GET_FORECAST_FROM_API({commit }) {
            try {
                const response = await fetch(`${BASE_API_URL}ca_clients/`);
                const data = await response.json();
                commit('SET_FORECAST_TO_STATE', data);
                return data; 
            } catch (error) {
                console.log(error);
            }
        },
    },
    getters: {
        SEARCH_QUERY(state) {
            return state.searchQuery;
        },
        FORECAST(state) {
            return state.weather;
        },
    }
});

const app = createApp({});
app.use(store);
export default store;
